import React,{useState, useEffect} from 'react';
import {useHistory} from 'react-router-dom'
import axios from 'axios';




const Contact = () =>{
    let history= useHistory();
    const [data, setData] = useState({
        name:"",
        email:"",
        phone:"",
        message:""
    });

    useEffect(() => {
        document.title='Contact';

     })
     const {name, email, phone, message}= data;
    const InputEvent= (event) =>{
        const {name, value} = event.target;
        setData((prev)=>{
            return{
         ...prev,
         [name]: value
            }
        });
    }

    const ContactformSubmission =(event)=>{
        event.preventDefault();
       
       

        
        alert(
            `name is ${data.name}. and your email is ${data.email} and phone number is ${data.phone}. and  ${data.message}. ` 
        )      
       
        async function postData(){
         await  axios.post("http://localhost:3003/Users" , data);
        }
        postData();
        history.push("/");
        // setData({
        //     name:'',
        //     email:'',
        //     phone:'',
        //     message:''
        // });
    }
    return(
        <>
        <div className="my-5">
            <h1 className="text-center"> Contact US</h1>
        </div>

        <div className="container contact_div"> 
            <div className="row">
                <div className="col-md-6 col-10 mx-auto">
                <form  onSubmit={ContactformSubmission}>
                <div class="form-group">
                        <label for="exampleInputname">User Name</label>
                        <input type="text"
                         className="form-control"
                          id="exampleInputname" 
                          aria-describedby="nameHelp"
                          placeholder="Enter username"
                          name="name"
                          onChange={InputEvent}
                          value={data.name}/>
                       
                    </div>
                    <div class="form-group">
                        <label for="exampleInputEmail1">Email address</label>
                        <input type="email"
                         className="form-control"
                          id="exampleInputEmail1" 
                          aria-describedby="emailHelp"
                          placeholder="Enter Email Address"
                          name="email"
                          onChange={InputEvent}
                          value={data.email}/>
                        <small id="emailHelp" className="form-text text-muted">We'll never share your email with anyone else.</small>
                    </div>
                    <div className="form-group">
                        <label for="exampleInputMobile">phone</label>
                        <input type="text"
                         className="form-control"
                          id="exampleInputphone"
                          placeholder="Enter Mobile"
                          name="phone"
                          onChange={InputEvent}
                          value={data.phone}
                          maxLength="10"/>
                    </div>
                    <div className="form-group">
                        <label for="exampleInpuMessage">Message</label>
                        <textarea type="password" 
                        className="form-control" 
                        id="exampleInpuMessage"
                        placeholder="Enter Message"
                          name="message"
                          onChange={InputEvent}
                          value={data.message}/>
                    </div>
                    <button type="submit" className="btn btn-primary">Submit</button>
                </form>
                </div>
            </div>
        </div>
        </>
    )
};

export default Contact;