import React, {useState, useEffect} from 'react';
import Axios from 'axios';
import { NavLink, Link } from 'react-router-dom';



const Users = () =>{

    const [userdetail, setUsers]= useState([]);

    useEffect(() => {
        
        getUserData();
    }, [])
    async function getUserData(){
        const res = await Axios.get("http://localhost:3003/Users/");
        setUsers(res.data.reverse())
    }

    const deleteUser = async id =>{
        await Axios.delete(`http://localhost:3003/Users/${id}`)
        getUserData()
    }

    return (

        <>
        <div className="container">
            <div className="py-4">
               <h1>Users Detail</h1>
            <table class="table border shadow">
            <thead class="thead-dark">
                <tr>
                <th scope="col"></th>
                <th scope="col">Email</th>
                <th scope="col">User Name</th>
                <th scope="col">Mobile</th>
                <th scope="col">Action</th>
                </tr>
            </thead>
            <tbody>
                    {
                   userdetail.map((val, index)=>(
                    <tr>
                        <th scope="row" key={index}></th>
                        <td>{val.email}</td>
                        <td>{val.name}</td>
                        <td>{val.phone}</td>
                        <td>
                            <NavLink className="btn btn-primary" to={`/viewuser/${val.id}`}>View</NavLink>
                            <NavLink className="btn btn-outLine-primary" to={`/Users/edit/${val.id}`}>Edit</NavLink>
                            <Link className="btn btn-danger"  onClick={()=> deleteUser(val.id)}>Delete</Link>
                        </td>
                    </tr>  
                        )
                    )};
            </tbody>
    </table>


                </div>
            </div>
        </>
    )
}


export default Users;